.row {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap; }

.col + .col {
  margin-left: 1.6%; }

.col-left {
  align-self: flex-start; }

.col-center {
  align-self: center; }

.col-right {
  align-self: flex-end; }

