    width: 6.86667%; }

  .ls2 {
    width: 15.33333%; }

  .ls3 {
    width: 23.8%; }

  .ls4 {
    width: 32.26667%; }

  .ls5 {
    width: 40.73333%; }

  .ls6 {
    width: 49.2%; }

  .ls7 {
    width: 57.66667%; }

  .ls8 {
    width: 66.13333%; }

  .ls9 {
    width: 74.6%; }

  .ls10 {
    width: 83.06667%; }

  .ls11 {
    width: 91.53333%; }

  .ls12 {
    width: 100%; } }
